<template>
	<view>
		<view class="content">
			
			<view class="title">目前只能预约当日24时前的充电任务</view>
			<view class="select">停车场:<input disabled="true" @click="openSelect" type="text" v-model="select" /></view>	
			<view class="time">到达时间:
			<input disabled v-model="select2" @click="selectDatePicker" />
			</view>
			<view class="time">充电时间:
			<input disabled v-model="select3" @click="openDuring()" type="text" />
			</view>
			<view class="time">车位选择:
				<input disabled v-model="select4" type="text" disabled="" @click="openSelectPark"/>
			</view>
			<view class="submit"><u-button @click="submitgo">确认预约</u-button></view>
			<u-select @confirm="confirm" v-model="showlist" :list="list"></u-select>
			<u-select @confirm="confirm2" v-model="showlist2" :list="timelist2"></u-select>
			<u-select @confirm="confirm3" v-model="showlist3" :list="parkid"></u-select>
			<u-modal content="预约成功" v-model="gogogo"></u-modal>
			 <date-time-picker ref='date-time'
			  :indicatorStyle='indicatorStyle'
			   :type='type'
				:datestring='dateString'
				 @change='dateTimeChange'></date-time-picker>
			
			
		</view>
	</view>
</template>

<script>
	import {config} from '../../static/js/config.js'
	import DateTimePicker from '../../components/bory-dateTimePicker/bory-dateTimePicker.vue'
	
	export default {
		components: {
		            DateTimePicker
		        },
		
		data() {
			return {
				select:'ifs充电桩',
				area_id:0,
				select2:'',
				time:'',
				select3:'',
				duringtime:'',
				select4:'',
				parkid:'',
				list:[],
				list2:[],
				showlist:false,
				timelist2:[{
					label:'30分钟',value:1
				},
				{
					label:'60分钟',value:2
				},
				{
					label:'90分钟',value:3
				},
				{
					label:'120分钟',value:4
				}],
				parkid:[{
					label:'1号车位',value:1
				},
				{
					label:'2号车位',value:2
				},
				{
					label:'3号车位',value:3
				},
				{
					label:'4号车位',value:4
				}],
				showlist2:false,
				showlist3:false,
				time:'',
				time2:'',
				dateString:'',
				type:'datetime',
				index:0,
				gogogo:false
			}
		},
        computed: {
            indicatorStyle() {
                return {
                    background: 'rgba(112, 251, 215, 0.5)',
                    height: '40px',
                };
            }
        },
		methods: {
			openDuring:function(){
				this.showlist2 = true
			},
			openSelectPark:function(){
				this.showlist3 = true
			},
			submitgo:function(){
				uni.showLoading({
					title:'提交中...'
				})
				let _this = this;
				setTimeout(()=>{
					uni.hideLoading()
					_this.gogogo = true
				},1200)
			},
			selectDatePicker:function() {
                this.type = 'hour-minute';
                this.index = 0;
                this.$refs['date-time'].show();
            },
			dateTimeChange(value) {
                this.select2 = value
            },
			confirm:function(e){
				this.select = e[0].label
				this.area_id = this.list[e[0].value]
				console.log(this.area_id)
			},
			confirm2:function(e){
				this.select3 = e[0].label
				
				console.log(e[0])
			},
			confirm3:function(e){
				this.select4 = e[0].label
				console.log(e[0].value)
			},
			openSelect:function(){
				//箭头函数会导致this失效，应该是双箭头作用域导致的，有机会研究下底层源码
				this.getList()
				//开始渲染列表
			},openSelectTime:function(){
				this.showlist2 = true
				//先写静态，因为逻辑比较难写
				//考虑转换为时间戳存入数据库
				
			},
			CheckAble:function(){
				//检测是否可用
				//如果可用生成订单。然后反馈到用户层
				//到达约定时间，超过5分钟，车没到库，取消订单。
				//1.把appoint的时间表取消掉
				//2.把payment的支付改为已取消
				//3.把订单表反馈回去
				
			},
			getList:function(){
				let lists = [];
				uni.showLoading({
					title:'加载中...'
				})
				uni.request({
					url:config.url + 'find',
					method:'GET',
					success: (res) => {
						for(let i in res.data){
							let list = {};
							list["value"] = i;
							list["label"] = res.data[i].names
							list["id"] = res.data[i].area_id
							list["nums"] = res.data[i].devices_num
							lists.push(list)
						}
						this.list = lists
						uni.hideLoading();
						this.showlist = true
						console.log(this.list)
					}
				})
			}
		}
	}
</script>

